import "./styles.css";
import Spline from "@splinetool/react-spline";

import { useState } from "react";
import React from "react";

export default function App() {
  const [value, setValue] = useState("");
  const [result, setResult] = useState("0");
  const [preres, setPreres] = useState("");

  var print = true;

  function onMouseDown(e) {
    if (e.target.name === "AC") {
      setValue((curValue) => "");
      setResult((curResult) => "0");
    } else if (e.target.name === "=") {
      var temp;
      setValue((curValue) => {
        console.log(curValue);
        if (curValue === undefined || curValue === "") {
          return "";
        } else if (curValue[curValue.length - 1] === "+") {
          return curValue;
        } else if (curValue[curValue.length - 1] === "-") {
          return curValue;
        } else if (curValue[curValue.length - 1] === "*") {
          return curValue;
        } else if (curValue[curValue.length - 1] === "/") {
          return curValue;
        } else if (curValue[curValue.length - 1] === "%") {
          return curValue;
        } else if (curValue[curValue.length - 1] === ".") {
          return curValue;
        } else {
          temp = curValue.toString().replace("=", ""); ///[^-()\d/*+.]/g
          let res = eval(temp);
          setResult((curResult) => res);
          setValue((curValue) => "");
          setPreres((curres) => {
            if (print === true) {
              print = false;
              return curres + res + "\n";
            } else if (print === false) {
              print = true;
              return curres;
            }
          });
        }
      });
    } else if (
      e.target.name === "+" ||
      e.target.name === "-" ||
      e.target.name === "*" ||
      e.target.name === "/" ||
      e.target.name === "%" ||
      e.target.name === "."
    ) {
      setValue((curValue) => {
        if (curValue[curValue.length - 1] === "+") {
          return curValue;
        } else if (curValue[curValue.length - 1] === "-") {
          return curValue;
        } else if (curValue[curValue.length - 1] === "*") {
          return curValue;
        } else if (curValue[curValue.length - 1] === "/") {
          return curValue;
        } else if (curValue[curValue.length - 1] === "%") {
          return curValue;
        } else if (curValue[curValue.length - 1] === ".") {
          return curValue;
        } else if (curValue === "") {
          return curValue;
        } else {
          return curValue + e.target.name;
        }
      });
      // var num = parseInt(e.target.name);
      // setResult((curResult) => curResult + e.target.name);
    } else if (e.target.name === "back") {
      setValue((curValue) => curValue.substring(0, curValue.length - 1));
    } else {
      setValue((curValue) => curValue + e.target.name);
    }
  }

  return (
    <div>
      <div className="App">
        <Spline
          scene="https://prod.spline.design/yfirLP42ugKex-8R/scene.splinecode"
          onMouseDown={onMouseDown}
        />
      </div>
      <div className="pos">
        <div className="display"> {value} </div>
      </div>
      <div className="preres">
        <pre>{preres}</pre>
      </div>
      <div className="result">{result}</div>
    </div>
  );
}
